// CertPlatzi Component

import React, { lazy, Suspense } from 'react'
import './cert-platzi.scss'
import {Link} from 'react-router-dom'

const Img = lazy(() => import('../../../../components/Img'))

const CertPlatzi = ({ name, url, img, school }) => {
  return (
    <div className="certPlatzi">
      <Link to={`/training/${url}`}>
        <Suspense>
          <Img path={img} alt={school} />
        </Suspense>
      </Link>
      <p className="">{name}</p>
    </div>
  )
}

export default CertPlatzi
